@media (--big) {

  .page__container{
    display: flex;
  }

  .page__main{
    flex-grow: 1;
  }

  .page__aside{
    width: 420px;
    margin-left: 8%;
    flex-shrink: 0;
  }

  .page__group{
    width: 30%;
    margin-right: 5%;

    &:nth-child(n+4){
      margin-top: 5rem;
    }

    &:nth-child(3n){
      margin-right: 0;
    }
  }

  .page__grid{
    display: flex;
    flex-wrap: wrap;
  }

  .page__map{
    width: 55%;
  }

  .page__contacts{
    width: 45%;
    padding-left: 2rem;
    padding-top: 4rem;
    box-sizing: border-box;
  }

  .page__group-info{
    width: 35%;
    box-sizing: border-box;
    margin-top: 3.5rem;

    &:nth-child(2n){
      margin-left: 15%;
    }
  }
}

@media (--medium) {

  .page__group{
    width: 40%;
    margin-right: 10%;

    &:nth-child(n+3){
      margin-top: 4rem;
    }

    &:nth-child(2n){
      margin-right: 0;
    }
  }

  .page__grid{
    display: flex;
    flex-wrap: wrap;
  }

  .page__group-info{
    width: 23%;
    box-sizing: border-box;
    margin-right: 2.3%;

    &:last-child{
      margin-right: 0;
    }
  }
}

@media screen and (width <= 1024px) {

  .page__aside{
    margin-top: 3rem;
  }

  .page__contacts{
    width: 100%;
    margin-bottom: 2rem;
  }

  .page__map{
    width: 100%;
  }
}

@media (--small) {

  .page__group:not(:last-of-type){
    margin-bottom: 2rem;
  }

  .page__group-info:not(:last-of-type){
    margin-bottom: 2rem;
  }

  .page__contacts{
    margin-bottom: 2rem;
  }
}

.page{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.page__inner{
  margin-top: 4rem;
}

.page__container{
  padding-bottom: 4rem;
}

.page__title{
  margin-bottom: 2.6rem;
}

.page__last-news{
  margin-bottom: 7rem;
}

.page__news:not(:last-of-type){
  margin-bottom: 4rem;
}

.page__section:not(:last-of-type){
  margin-bottom: 5rem;
}

.page__map{
  position: relative;
  display: flex;
}

.page__map-image{
  margin: auto;
}

.page__map-container{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.page__contacts-name{
  font-size: 1.8rem;
}

.page__contacts-value{
  font-size: 1.6rem;
  margin-top: 1rem;
}
